import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo2 from '../images/logo2.png'
import Image from 'react-bootstrap/Image'
import { LinkContainer } from 'react-router-bootstrap';
import  { useNavigate } from 'react-router'


function Header(props) {
  const navigate = useNavigate()
  const handleLogout=()=>{
    localStorage.removeItem('token');
    navigate('/login')
  }
  return (
   <>
       <style type="text/css">
        {`
   .dropdown-menu {
    background: ${props.bcolor};
   

  }
   .dropdown-menu .dropdown-item{
    padding:0.5rem 4rem
  }
  @media(max-width:768px){
    .container{
     flex-direction:row!important;
     padding:1rem !important:
    
    }
    
    .navbar .container {
    padding:0 1rem; 
    flex-direction:row!important;
  }
  }

  @media (max-width: 992px){
  .navbar .container {
    max-width: 910px!important;
    flex-direction:row!important;
}
}

@media (max-width:778px){
  .carousel-item img{
    height:300px!important;
  }
}
    `}
      </style>
      
    <Navbar bg="light" expand="lg">
      <Container >
        <Image src={logo2} style={{width:'60px',height:"70px",marginRight:'85px' ,marginTop:'5px'}}></Image>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0 text-center"
            style={{ maxHeight: '300px' }}
            navbarScroll
          >
           <LinkContainer to="/">
              <Nav.Link className="fs-5 px-4 " >Home</Nav.Link>
              </LinkContainer>

              <LinkContainer to="/about">
            <Nav.Link  className="fs-5 px-4">About</Nav.Link>
            </LinkContainer>

              <LinkContainer to="/services">
            <Nav.Link  className="fs-5 px-4">Services</Nav.Link>
            </LinkContainer>

    
              <LinkContainer to="/models">
            <Nav.Link  className="fs-5 px-4">Models</Nav.Link>
            </LinkContainer>

    
           
            <LinkContainer to="/contact">
            <Nav.Link  className="fs-5">Contact</Nav.Link>
            </LinkContainer>
           
            
          </Nav>
          {!localStorage.getItem('token')?  <Form className="d-flex justify-content-center">
   
          <LinkContainer to="/Login">
            <Button variant="dark" className=" rounded-0 mx-2 px-3">Login</Button>
            </LinkContainer>
            <LinkContainer to="/Signup">
            <Button variant="outline-dark" className="rounded-0">Signup</Button>  
            </LinkContainer>
          </Form>: <Button variant="dark" onClick={handleLogout} className="rounded-0">Logout</Button>}
        </Navbar.Collapse>
      </Container>
    </Navbar>
    </> 
  );

}

export default Header;