import * as Yup from "yup";


export const contactSchema=Yup.object({
    fname:Yup.string().min(2).max(15).required("Please enter your first name"),
    lname:Yup.string().min(2).max(15).required("Please enter your last name"),
    email:Yup.string().email().required("please enter your email"),
    phone:Yup.string().min(11).max(14).required("please enter your phone"),
    zip:Yup.string().min(4).max(15).required("Please enter your zip"),
    state:Yup.string().min(2).max(15).required("Please Select your State"),
    city:Yup.string().min(4).max(15).required("Please enter city"),
    message:Yup.string().min(5).required("Please enter your message")
    // termsAndConditions:Yup.bool().oneOf([true], 'You need to accept the terms and conditions')
})

export const loginSchema=Yup.object({
       email:Yup.string().email().required("please enter your email"),
       Password:Yup.string().required('please enter your password')
       
})


export const signUpSchema=Yup.object({
    name:Yup.string().min(3).required("Please enter your name"),
    email:Yup.string().email().required("please enter your email"),
    Password:Yup.string().required('please enter your password'),
    CPassword:Yup.string().required('please enter Confirm passowrd').oneOf(
          [Yup.ref("Password")],
          "Both password need to be the same"
        )
})
