import React from 'react'

function Features(props) {
  return (
    <>
    <style type="text/css">
     {`

.container .features-container .feature-card:hover{
    cursor:pointer!important;
   opacity:0.7;
}

@media(max-width:768px){
    .container .features-container{
        flex-direction:column;
    }
    .container .features-container .feature-card{
        width:100%!important;
        border-left:none!important;
        border-bottom:1px solid grey;
        padding:1rem 0;
        
    }
  
}

 `}
   </style>
      <div className="container-fluid" style={{background:"#F8F9FA"}}>
    <div className="container py-5" >
        <div className="features-container d-flex justify-content-around ">
            <div className="feature-card px-5 text-center border-grey border-start w-25 align-self-center">
            <i className='bx bx-cart fs-3'></i>
                <h5>View Cart</h5>
            </div>
       
            <div className="feature-card px-5 text-center border-grey border-start w-25 align-self-center">
            <i className='bx bx-news fs-3 text-center'></i>
                <h5>Nesletter</h5>
            </div>
       
            <div className="feature-card px-5 text-center border-grey border-start w-25 align-self-center">
            <i className='bx bxs-map fs-3'></i>
                <h5>Locate Dealer Ship</h5>
            </div>
       
            <div className="feature-card px-5 text-center border-grey border-start w-25 align-self-center">
            <i className='bx bxs-briefcase fs-3'></i>
                <h5>Services</h5>
            </div>
       
           
        </div>
       
    </div>
    </div>
    </>
  )
}

export default Features