import React, { useState } from 'react'
import { useNavigate } from 'react-router'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Image from 'react-bootstrap/Image';
import car31 from '../images/car31.jpg'
import { useFormik } from "formik"
import { signUpSchema } from '../schemas'


function Signup() {

  const [email, setEmail] = useState(true)
  const navigate = useNavigate()


  const initialValues = {
    name: "",
    email: "",
    Password: "",
    CPassword: ""
  }

  const { values, touched, errors, handleBlur, handleChange, handleSubmit, isSubmitting, resetForm } = useFormik({
    initialValues: initialValues,
    validationSchema: signUpSchema,
    onSubmit: async (values) => {
     const response = await fetch(`http://localhost:3000/api/auth/createuser`, {
      // const response = await fetch(`https://bmwcar.herokuapp.com//api/auth/createuser`, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        headers: {
          'Content-Type': 'application/json',


        },
        body: JSON.stringify(values)
      });
      const json = await response.json()
      //  console.log(json)
      //  console.log(json.error)


      if (json.success) {
        localStorage.setItem('token', json.authtoken)
        alert("signed  in success")
        navigate("/")
      } else {
        alert('signed in fail')

        setEmail(false)
      }
      resetForm();
    }
  })

  console.log(errors)


  return (
    <>



      <style type="text/css">
        {`
        
p{
  margin-bottom:0!important;
  position:relative!important;
  left:65px!important;
}
@media (max-width: 1200px){
  p {
    left:55px!important;
  }
  .form-label{
    position:relative;
    right:10px;
    }

  @media (max-width: 992px){
  .container {
    max-width: 810px!important;
}
p {
  left:45px!important;
}
.form-label{
  right:20px;
  }
}
@media (max-width: 768px){
  .container .left{
  display:none!important;
}
.container .right{
  width:80%!important;
  margin:0 auto;
}
.form-label{
  right:0;
    margin-left:0!important;
    padding-left:0!important;
  }
#formBasicEmail,#formBasicPassword,#formBasicPassword1{
  width:100% !important;
}

.btn{
  width:100% !important;
}
p{
  left:0px!important;
}
}
    `}
      </style>

      <h1 className="fw-light text-center  py-3">Create a Account Now</h1>
      <div className="container d-flex shadow-lg py-5 my-3 px-5">
        <div className="left w-50 pe-5">
          <Image src={car31} style={{ width: "100%" }} />
        </div>
        <div className="right   w-50">
          <h1 className="fw-light text-center py-2"> Sign Up</h1>
          <Form onSubmit={handleSubmit}>
            <Form.Group className="mb-3 " controlId="formBasicEmail">
              <Form.Label className="ms-5 px-3">Full Name</Form.Label>
              <Form.Control className="w-75 rounded-0 d-block mx-auto" type="text" value={values.name} onChange={handleChange} onBlur={handleBlur} name="name" placeholder="Enter email" />
              {errors.name && touched.name ? (<p className="text-danger">{errors.name}</p>) : null}
            </Form.Group>
            <Form.Group className="mb-3 " controlId="formBasicEmail">
              <Form.Label className="ms-5 px-3">Email address</Form.Label>
              <Form.Control className="w-75 rounded-0 d-block mx-auto" type="email" value={values.email} onBlur={handleBlur} onChange={handleChange} name="email" placeholder="Enter email" />
              {errors.email && touched.email ? (<p className="text-danger">{errors.email}</p>) : null}
              {email ? null : (<p className="text-danger">Email is Already Registered</p>)}
              {console.log(email)}
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label className="ms-5 px-3">Password</Form.Label>
              <Form.Control className="w-75 rounded-0 d-block mx-auto" type="password" value={values.Password} onBlur={handleBlur}  name="Password" onChange={handleChange}  placeholder="Password" />
              {errors.Password && touched.Password ? (<p className="text-danger">{errors.Password}</p>) : null}
            </Form.Group>

            <Form.Group className="mb-3" controlId="formBasicPassword1">
              <Form.Label className="ms-5 px-3">Confirm Password</Form.Label>
              <Form.Control className="w-75 rounded-0 d-block mx-auto" onBlur={handleBlur} onChange={handleChange} value={values.CPassword} name="CPassword" type="password" placeholder="Confirm Password" />
              {errors.CPassword && touched.CPassword ? (<p className="text-danger">{errors.CPassword}</p>) : null}
            </Form.Group>

            <Button variant="dark" className="w-75 rounded-0 d-block mx-auto" disabled={isSubmitting} type="submit">
              Signup
            </Button>
          </Form>

        </div>
      </div>

    </>
  )
}

export default Signup