import React from 'react'
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { LinkContainer } from 'react-router-bootstrap';
import { contactSchema } from '../schemas'
import { useFormik } from "formik"



function Contact() {

  const initialValues = {

    fname: "",
    lname: "",
    phone: "",
    email: "",
    city: "",
    state: "",
    zip: "",
    message: "",
    // termsAndConditions: false
  };

  // const Formik=useFormik({})
  // console.log(Formik);

  const { values, touched, errors, handleBlur, handleChange, handleSubmit, isSubmitting,resetForm } = useFormik
    ({

      initialValues: initialValues,
      validationSchema: contactSchema,
      onSubmit: async (values) => {
         //const response = await fetch(`http://localhost:3000/api/contactform/submit`, {
         const response = await fetch(`https://bmwcar.herokuapp.com//api/contactform/submit`, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(values),

        });
        const json = await response.json()
        console.log(json);
        console.log(json.errors);
        if (json.success) {
          alert("submitted successefully")
        } else {
          alert("submit  failed")
        }
        resetForm();
      }
    })
  //  console.log(errors)


  return (
    <>
      <style type="text/css">
        {`
    

.breadcrumb {
margin-bottom:0;
padding:10px 15px;
}
    .breadcrumb-item a{
       color:black;
   }
p{
  font-size:13px!important;
  margin-bottom:0!important;
}
@media(max-width:1200px){
  .container{
    max-width:1000px;
    padding:1rem;
  }
}

@media(max-width:1100px){
  .container{
    max-width:950px;
    padding:1rem;
  }
}
@media(max-width:992px){
  .container {
  flex-direction:column!important;
  }

.container .left,.right{
  width:100%!important;
  padding:1rem;
}
}

@media(max-width:480px){
  .container{
    padding:0.5rem!important;
  }
  .container .left{
    padding-right:12px!important;
  }
}

`}
      </style>
      <Breadcrumb className="shadow-sm mb-0">
        <LinkContainer to="/">
          <Breadcrumb.Item>Home</Breadcrumb.Item>
        </LinkContainer>
        <Breadcrumb.Item active>Contact</Breadcrumb.Item>
      </Breadcrumb>
      <div className="container d-flex shadow-lg py-5 my-5 px-5">
        <div className="left w-50 pe-5">
          <h1 className="fw-light">CONTACT US</h1>
          <p className="fw-light">We are committed to fulfilling the needs and requirements of your organization and individuals over the long term. We provide products and services tailored to your needs, from vehicle type, to financing and leasing options. Contact us to know if you are eligible for any of the above programs, our dedicated team of professionals is always a call away.</p>
        </div>
        <div className="right w-50">
          <Form onSubmit={handleSubmit}>

            <Row className="mb-3">
              <Form.Group as={Col} controlId="fname">
                <Form.Label>First Name</Form.Label>
                <Form.Control className="rounded-0" name="fname" value={values.fname} onChange={handleChange} onBlur={handleBlur} type="text" placeholder="" />
                {errors.fname && touched.fname ? (<p className="text-danger">{errors.fname}</p>) : null}
              </Form.Group>


              <Form.Group as={Col} controlId="lname">
                <Form.Label>Last Name</Form.Label>
                <Form.Control className="rounded-0" name="lname" value={values.lname} onBlur={handleBlur} onChange={handleChange} type="text" placeholder="" />
                {errors.lname && touched.lname ? (<p className="text-danger">{errors.lname}</p>) : null}
              </Form.Group>
            </Row>

            <Row className="mb-3">
              <Form.Group as={Col} controlId="email">
                <Form.Label>Email</Form.Label>
                <Form.Control className="rounded-0" type="email" name="email" onBlur={handleBlur} value={values.email} onChange={handleChange} placeholder="" />
                {errors.email && touched.email ? (<p className="text-danger">{errors.email}</p>) : null}
              </Form.Group>

              <Form.Group as={Col} controlId="phone">
                <Form.Label>Phone</Form.Label>
                <Form.Control className="rounded-0" type="phone" name="phone" onBlur={handleBlur} value={values.phone} onChange={handleChange} placeholder="" />
                {errors.phone && touched.phone ? (<p className="text-danger">{errors.phone}</p>) : null}
              </Form.Group>
            </Row>

            <Row className="mb-3 ">
              <Form.Group as={Col} controlId="city">
                <Form.Label>City</Form.Label>
                <Form.Control className="rounded-0" name="city" value={values.city} onBlur={handleBlur} onChange={handleChange} />
                {errors.city && touched.city ? (<p className="text-danger">{errors.city}</p>) : null}
              </Form.Group>

              <Form.Group as={Col} controlId="state">
                <Form.Label>Province/state</Form.Label>
                <Form.Select className="rounded-0" name="state" value={values.state} onBlur={handleBlur} onChange={handleChange} >
                  <option>Choose...</option>
                  <option>Punjab</option>
                  <option>Sindh</option>
                  <option>KPK</option>
                  <option>Bolochistan</option>
                </Form.Select>
                {errors.state && touched.state ? (<p className="text-danger">{errors.state}</p>) : null}
              </Form.Group>

              <Form.Group as={Col} controlId="zip">
                <Form.Label>Zip</Form.Label>
                <Form.Control className="rounded-0" name="zip" value={values.zip} onBlur={handleBlur} onChange={handleChange} />
                {errors.zip && touched.zip ? (<p className="text-danger">{errors.zip}</p>) : null}
              </Form.Group>
            </Row>

            <Form.Group className="mb-3 rounded-0" controlId="message">
              <Form.Label>Message</Form.Label>
              <Form.Control className="rounded-0" name="message" value={values.message} onBlur={handleBlur} onChange={handleChange} as="textarea" rows={3} />
              {errors.message && touched.message ? (<p className="text-danger">{errors.message}</p>) : null}
            </Form.Group>

            <Form.Group className="mb-3" id="formGridCheckbox">
              <Form.Check type="checkbox" className="fw-light" name="termsAndConditions" label="I understand that all my personal data will be dealt with in accordance to the relevant privacy protection regulations.*" />
              {/* {errors.termsAndConditions ? (<p className="text-danger" >{errors.termsAndConditions}</p>) : null} */}

            </Form.Group>

            <Button variant="dark" className="rounded-0 w-50" disabled={isSubmitting} type="submit">
              Submit
            </Button>
          </Form>

        </div>
      </div>
    </>
  )
}

export default Contact