import React from 'react'
import  { useNavigate } from 'react-router'
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Image from 'react-bootstrap/Image';
import car18 from '../images/car18.jpg'
import { useFormik } from "formik"
import { loginSchema } from '../schemas'


function Login() {
  
   const initialValues={
     email:"",
     Password:""
   }

   const { values, touched, errors, handleBlur, handleChange, handleSubmit, isSubmitting,resetForm }=useFormik({
    initialValues:initialValues,
    validationSchema:loginSchema,
    onSubmit: async (values)=>{
     const response = await fetch(`http://localhost:3000/api/auth/login`, {
   // const response = await fetch(`https://bmwcar.herokuapp.com/api/auth/login`, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          headers: {
            'Content-Type': 'application/json',
      
          
          },   
          body: JSON.stringify(values)
        });
        const json= await response.json()
        console.log(json)
        if(json.success){
          localStorage.setItem('token',json.authtoken)
            alert("logged in success")
            navigate("/")
        }else{
                   alert('logged in fail')
        }
        resetForm();
  }
  })
  
    const navigate = useNavigate()
  
  return (
    <>

<style type="text/css">
        {`

p{
  margin-bottom:0!important;
  position:relative!important;
  left:65px!important;
}

@media (max-width: 1200px){
p {
  left:55px!important;
}
.form-label{
position:relative;
right:10px;
}
}
  @media (max-width: 992px){
  .container {
    max-width: 810px!important;
}
p {
  left:45px!important;
}
.form-label{
  right:20px;
  }
}
@media (max-width: 768px){
  .container .left{
  display:none!important;
}
.container .right{
  width:80%!important;
  margin:0 auto;
}
.form-label{
right:0;
  margin-left:0!important;
  padding-left:0!important;
}
#formBasicEmail,#formBasicPassword{
  width:100% !important;
}

.btn{
  width:100% !important;
}
p{

  left:0px!important;
}

}
    `}
      </style>
       <h1 className="fw-light text-center  py-3">Login into Your Account </h1>
    <div className="container d-flex shadow-lg py-5 my-3 px-5">
    <div className="left w-50 pe-5">
     <Image src={car18} style={{width:"100%"}}/>
    </div>
    <div className="right   w-50">
        <h1 className="fw-light text-center py-2"> Login</h1>

    <Form onSubmit={handleSubmit}>
      <Form.Group className="mb-3 " controlId="formBasicEmail">
        <Form.Label className="ms-5 px-3">Email address</Form.Label>
        <Form.Control className="w-75 rounded-0 d-block mx-auto"  value={values.email} onChange={handleChange} onBlur={handleBlur} name='email'  type="email" placeholder="Enter email" />
        {errors.email && touched.email ? (<p className="text-danger">{errors.email}</p>) : null}
      </Form.Group>

      <Form.Group className="mb-3" controlId="formBasicPassword">
        <Form.Label className="ms-5 px-3">Password</Form.Label>
        <Form.Control className="w-75 rounded-0 d-block mx-auto"  type="password" value={values.Password}
       onChange={handleChange} onBlur={handleBlur} name="Password" placeholder="Password" />
        {errors.Password && touched.Password ? (<p className="text-danger">{errors.Password}</p>) : null}
        
      </Form.Group>
     
      <Button variant="dark" className="w-75 rounded-0 d-block mx-auto" disabled={isSubmitting} type="submit">
        Login
      </Button>
    </Form>

    </div>
</div>

</>
  )
}

export default Login