import Carousel from 'react-bootstrap/Carousel';
import car7 from '../images/car7.jpg'
import Features from './Features';
import BmWorld from './BmWorld';
import car33 from '../images/car33.jpg'
import car6 from '../images/car6.jpg'

function Home(props) {
  return (
    <>


    <Carousel fade >
      <Carousel.Item>
        <img
          className="d-block w-100"
          style={{objectFit:'cover'}}
           height={500}
          src={car7}
          alt="First slide"
        />
        <Carousel.Caption>
          <h3>The New i7</h3>
          <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          style={{ objectFit:'cover'}}
          height={500}
          src={car33}
          alt="Second slide"
        />

        <Carousel.Caption>
          <h3>The M4 Competition</h3>
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
        <img
          className="d-block w-100"
          style={{objectFit:'cover'}}
          
          height={500}
          src={car6}
          alt="Third slide"
        />

        <Carousel.Caption>
          <h3>The BMW X7</h3>
          <p>
            Praesent commodo cursus magna, vel scelerisque nisl consectetur.
          </p>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>

    <Features/>
    <BmWorld/>
    </>
  );
}

export default Home;