import React,{useContext, useEffect} from 'react'
import { Image} from 'react-bootstrap'
import NoteContext from '../context/notes/NoteContext'
import Card from 'react-bootstrap/Card'
import Button from '@restart/ui/esm/Button'
import Breadcrumb from 'react-bootstrap/Breadcrumb';
import { LinkContainer } from 'react-router-bootstrap';
import car10 from '../images/car10.jpg'
import s5 from '../images/s5.jpg'
function Models() {
  const context = useContext(NoteContext)
  const { cars, getCars } = context;
  //    const {updateNote}=props;



  useEffect(() => {

      getCars()
      console.log(getCars())
      // eslint-disable-next-line
  }, [])


  return (
      <>
       <style type="text/css">
        {`
        

.breadcrumb {
margin-bottom:0;
padding:10px 15px;
}
        .breadcrumb-item a{
           color:black;
       }
       @media(max-width:992px){
        .container{
            max-width:900px;
            
        }
        @media(max-width:786px){
          .card-title{
            padding:3px 0px!important;
          }
          .card-img-overlay{
            flex-direction:column!important;
            justify-content:center!important;
            align-items:center!important;
          }
          .model-footer-card .card-text{
          width:80%!important;
          }
        }
    `}
      </style>
<Breadcrumb className="shadow-lg mb-0">
        <LinkContainer to="/">
      <Breadcrumb.Item>Home</Breadcrumb.Item>
      </LinkContainer>
        <Breadcrumb.Item active>Models</Breadcrumb.Item>
    </Breadcrumb>

    <div className="container-fluid px-0" >
        <Card className="bg-dark text-white rounded-0">
            <Card.Img src={car10} alt="Card image" height={150} style={{ objectFit: 'cover', opacity: "0.4" }} />
            <Card.ImgOverlay className="d-flex justify-content-between">
                <Card.Title className="fs-3 py-5 fw-semi-bold">BMW Models</Card.Title>
                <div className="align-self-center">
                <Button className="btn btn-outline-light rounded-0">View Offer</Button>
                </div>
            </Card.ImgOverlay>
        </Card>
    </div>

    <div className="container py-4">
            <div className="row row-cols-1 row-cols-md-4 g-4">
            {cars.map((element) => {
               return <div className="col" key={element.id}>
                    <div className="card rounded-0 border-0 shadow-sm">
                        <Image src={element.img} height={200} className="card-Image-top" alt="bmw-cars" />
                        <div className="card-body">
                            <h5 className="card-title pb-4">{element.name}</h5>
                            <button hef="/" className="btn btn-outline-dark  d-block rounded-0 mb-5">Discover More</button>
                        </div>
                    </div>
                </div>
            })};
            </div>
        </div>

    <div className="container-fluid model-footer-card px-0 py-5" >
        <Card className="bg-dark text-white rounded-0">
            <Card.Img src={s5} alt="Card image" height={450} style={{ objectFit: 'cover', opacity: "0.4" }} />
            <Card.ImgOverlay className="d-flex flex-column justify-content-center align-items-center">
                <Card.Title className="fs-4 fw-semibold">Find My BMW Online</Card.Title>
                <Card.Text className="text-center py-3 w-50">Enter a new league of luxury with the all-electric extraordinaire. Bold and brilliant from every angle,  unapologetic performance. </Card.Text>
               <button className='btn btn-light rounded-0'>View Stock</button>
            </Card.ImgOverlay>
        </Card>
    </div>

   

    </>
  )
}

export default Models