import React from "react";
import NoteContext from "./NoteContext";
import { useState } from "react";


const NoteState=(props)=>{
   const host="http://localhost:3000"
  // const host="https://bmwcar.herokuapp.com"
  const [user, setUser] = useState({name:"Jesse Hall"});
    const s1={
        "name":"harry",
        "class":"5b"
    }
const [state,setState]=useState(s1);
const update=()=>{
    setTimeout(()=>{
        setState({
            "name":"Shery",
            "class":"bsit"
        })
    },4000)}


   const carsInitial=[]
   const  [cars,setCars]=useState(carsInitial)
//===========================================GET All Notes==================================

    const getCars= async ()=>{
    //   const response = await fetch(`${host}/api/notes/fetchallnotes`, {
      const response = await fetch(`${host}/api/cars/getcars`, {
        method: 'GET', // *GET, POST, PUT, DELETE, etc.
        headers: {
          'Content-Type': 'application/json',
        //   'auth-token': localStorage.getItem('token')
        
        },
        
      });
      const json= await response.json()
      console.log(json)

      setCars(json)
    }

    
    return(

        <NoteContext.Provider value={{user,setUser,state,update,cars,setCars,getCars}}>
            {props.children}
        </NoteContext.Provider>
    )
}

export default NoteState;